.main-work {
  margin: auto;
  min-height: 250px;
  padding: 5px;
  padding-right: 10px;
}

.whole {
  display: none;
  position: fixed;
  height: 100vh;
  width: 100vw;
  background-color: #222;
  opacity: 0.75;
  top: 0;
  left: 0;
  z-index: 100;
  text-align: center;
  color: white;
  font-size: 35px;
  vertical-align: middle;
  line-height: 90vh;
  pointer-events: none;
}

.whole-show {
  display: block;
}

.FileInput {
  display: none;
}

.dropzone-dummy {
  width: 100%;
  height: 200px;
  border: 3px dashed #75daff;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 16px;
}

.Highlight {
  border: 3px solid #75daff;
}

.upload-button {
  margin-top: 14px;
  background-color: #20c9ee;
  border: none;
  padding: 10px;
  border-radius: 3px;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  color: white;
  font-size: 16px;
  cursor: pointer;
}

.details {
  height: 175px;
  margin: 10px;
  display: flex;
}

.details-left {
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.details-text {
  margin: 15px;
  margin-right: 20px;
  font-size: 18px;
  display: flex;
  justify-content: space-between;
  flex-flow: row-reverse;
}

.details-name {
  display: block;
  font-size: 17.5px;
}

.details-dimention {
  display: block;
}

.details-thumbnail-wrapper {
  display: flex;
  justify-content: flex-end;
}

.details-thumbnail {
  height: 120px;
  width: 120px;
  border-radius: 10px;
}

.details-middle {
  width: 20%;
  height: 100%;
  display: flex;
  align-content: middle;
}

.details-right {
  width: 40%;
  height: 100%;
  text-align: center;
}

.details-change-wrapper {
  margin: 0px;
  margin-top: 15px;
  text-align: center;
}

.details-change {
  font-size: 20px;
  width: 65px;
  border: 1px solid #999;
  border-radius: 4px;
}

.details-button {
  margin-top: 18px;
  background-color: #20c9ee;
  border: none;
  padding: 8px;
  border-radius: 3px;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  color: white;
  font-size: 18px;
  cursor: pointer;
  width: 180px;
}

.details-button-clicked {
  background: linear-gradient(45deg, #fe6b8b 30%, #ff8e53 90%);
}

#widthChange {
  margin-right: 20px;
}

#heightChange {
  margin-left: 20px;
}

#middle-x {
  font-size: 21px;
}

@media screen and (max-width: 720px) {
  .details-name {
    display: none;
  }

  .details-text {
    align-content: center;
  }

  .details-button {
    width: 120px;
    font-size: 16px;
  }

  #widthChange {
    margin-right: 5px;
  }

  #heightChange {
    margin-left: 5px;
  }

  .details-change {
    font-size: 18px;
    width: 45px;
  }
}
